html, body, #root {
  margin: 0;
	height:100%;
  width: 100%;
}
  
/*整體區塊*/
.MainBlock {
	  height:100%;
    width: 100%;
}
  
/*最上方區塊*/
#topBlock{
    display: flex;
    background: #ff7380;
    height:8%;
	  width:100%;
    text-align: center;
    justify-content: space-between;
    align-items: center;
}

  
  /*最上方區塊之中間字體樣式*/
.topText{
    color: #ffffff;         
    font-size: 55px;       
    font-family: SignPainter-HouseScript;
}

  /*最上方區塊之按鈕樣式*/
.topButton{
    outline:none;
    border:none; 
    background: #ff7380;
    margin-left:  20px;
    margin-right: 20px;               
}